
import { defineComponent, toRefs, PropType } from "vue";
import { useRouter } from "vue-router";
import { ElNotification } from "element-plus";
import { formatImageUrl } from "@/utils/image";
import { FabricResult } from "@/typings/fabric";

const Component = defineComponent({
  name: "FabricCard",
  props: {
    data: {
      type: Object as PropType<FabricResult>,
      default: () => ({})
    },
    hasStatus: {
      type: Boolean,
      default: false
    },
    hasButtons: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    const router = useRouter();
    const { data, hasButtons } = toRefs(props);

    const onImageClick = async () => {
      await router.push({
        name: "FabricDetail",
        params: { id: data.value.id }
      });
    };

    const onFavoriteClick = async () => {
      if (!hasButtons) return;
      emit("add-to-favorite", data.value.id);
    };

    const onAddClick = () => {
      if (!hasButtons) return;
      ElNotification({
        title: "Success",
        type: "success",
        message: `Add`
      });
    };

    return {
      onImageClick,
      onFavoriteClick,
      onAddClick,
      formatImageUrl
    };
  }
});

export default Component;
